  40% {
    transform: translateX(40px); }
  60% {
    transform: translateX(20px); }
  90% {
    transform: translateX(5px); }
  20%, 50%, 80%, 100% {
    opacity: 1;
    z-index: 100;
    transform: translateX(0); } }

.fadeInTop {
